<template>
  <div id="loginbox-input">
    <!-- 登录 - option -->
    <div class="option">
      <span :class="{active:show.qr}" @click="change('qr')">
        <a href="javascript:;" class="normalFont">扫码登录</a>
      </span>
      <span :class="{active:show.input}" @click="change('input')">
        <a href="javascript:;" class="normalFont">账户登录</a>
      </span>
    </div>
    <LoginInputAccount :class="{boxShow:show.input}"></LoginInputAccount>
    <LoginInputQR :class="{boxShow:show.qr}"></LoginInputQR>
  </div>
</template>

<script>
import LoginInputQR from "@/components/Login/Middle/LoginBox/LoginBox_Components/LoginBox_Input/LoginInput_QR.vue";
import LoginInputAccount from "@/components/Login/Middle/LoginBox/LoginBox_Components/LoginBox_Input/LoginInput_Account.vue";
export default {
  name: "LoginBoxInput",
  components: {
    LoginInputAccount,
    LoginInputQR,
  },
  data() {
    return {
      show: {
        input: false,
        qr: true,
      },
    };
  },
  methods: {
    change(option) {
      for (let item in this.$data.show) {
        this.$set(this.$data.show, item, false);
      }
      this.$set(this.$data.show, option, true);
    },
  },
};
</script>

