<template>
  <!-- 广告分割 -->
  <div class="background">
    <div class="content" style="margin:0px auto">
      <HeadShopBigImg></HeadShopBigImg>
      <ShopHeadUserNav></ShopHeadUserNav>
    </div>
  </div>
</template>

<script>
import HeadShopBigImg from "@/components/Detail/ShopHead/ShopHead_Components/ShopHead_BigImg.vue";
import ShopHeadUserNav from "@/components/Detail/ShopHead/ShopHead_Components/ShopHead_UserNav.vue";

export default {
  name: "ShopHead",
  components: {
    HeadShopBigImg,
    ShopHeadUserNav,
  },
  mounted() {
    var content = document.querySelector("div.background>div.content");
    window.onresize = (e) => {
      if (window.innerWidth <= 1920) {
        content.style.margin = `0px auto 0px ${
          (e.currentTarget.innerWidth - 1920) / 2
        }px`;
      } else {
        content.style.margin = "0px auto";
      }
    };
  },
};
</script>

<style scoped>
div.background > div.content {
  width: 1920px;
}
</style>