<template>
  <div class="container index">
    <Search :show="searchIsShow"></Search>
    <Shortcut></Shortcut>
    <Header></Header>
    <GouWuDaoHang></GouWuDaoHang>
    <div class="elevator-content">
      <Elevator :elevator="props"></Elevator>
      <SecKill></SecKill>
      <SpecSale></SpecSale>
      <NiceGoods></NiceGoods>
      <Core4></Core4>
      <Horizon channelName="推荐频道"></Horizon>
      <Channels></Channels>
      <Horizon channelName="为你推荐"></Horizon>
      <Feeds></Feeds>
      <Footer></Footer>
    </div>
  </div>
</template>

<script>
import Shortcut from "@/components/Index/Shortcut/Shortcut.vue";
import Header from "@/components/Index/Header/Header.vue";
import GouWuDaoHang from "@/components/Index/GouWuDaoHang/GouWuDaoHang.vue";
import SecKill from "@/components/Index/SecKill/SecKill.vue";
import SpecSale from "@/components/Index/SpecSale/SpecSale.vue";
import NiceGoods from "@/components/Index/NiceGoods/NiceGoods.vue";
import Core4 from "@/components/Index/Core4/Core4.vue";
import Horizon from "@/components/Index/Horizon/Horizon.vue";
import Channels from "@/components/Index/Channels/Channels.vue";
import Feeds from "@/components/Index/Feeds/Feeds.vue";
import Footer from "@/components/Index/Footer/Footer.vue";
import Search from "@/components/Index/Search/Search.vue";
import Elevator from "@/components/Index/Elevator/Elevator.vue";

export default {
  name: "Index",
  data() {
    return {
      searchIsShow: false,
      props: {
        elevatorFixed: false,
        elevatorActive: [false, false, false, false],
      },
    };
  },
  components: {
    Shortcut,
    Header,
    GouWuDaoHang,
    SecKill,
    SpecSale,
    NiceGoods,
    Core4,
    Horizon,
    Channels,
    Feeds,
    Footer,
    Search,
    Elevator,
  },
  mounted() {
    var clear = () => {
      for (var i = 0; i < this.$data.props.elevatorActive.length; i++) {
        this.$set(this.$data.props.elevatorActive, i, false);
      }
    };
    window.onscroll = (e) => {
      var scrollTop =
        document.documentElement.scrollTop || document.body.scrollTop;
      if (scrollTop >= 682) {
        this.$data.searchIsShow = true;
        this.$data.props.elevatorFixed = true;
      } else {
        this.$data.searchIsShow = false;
        this.$data.props.elevatorFixed = false;
      }

      if (scrollTop >= 682 && scrollTop < 894) {
        clear();
        this.$data.props.elevatorActive[0] = true;
      } else if (scrollTop >= 894 && scrollTop < 1888) {
        clear();
        this.$data.props.elevatorActive[1] = true;
      } else if (scrollTop >= 1888 && scrollTop < 2920) {
        clear();
        this.$data.props.elevatorActive[2] = true;
      } else if (scrollTop >= 2920) {
        clear();
        this.$data.props.elevatorActive[3] = true;
      } else {
        clear();
      }
    };
  },
};
</script>

