<template>
  <div id="register">
    <Content></Content>
    <MaskLayout :class="{disppear:isShow}"></MaskLayout>
    <Protocol :class="{disppear:isShow}" @disppear="disppear"></Protocol>
  </div>
</template>

<script>
import Content from "@/components/Register/Content/Content.vue";
import MaskLayout from "@/components/Register/Mask/Mask.vue";
import Protocol from "@/components/Register/Protocol/Protocol.vue";

export default {
  name: "Register",
  components: {
    Content,
    MaskLayout,
    Protocol,
  },
  data() {
    return {
      isShow: true,
    };
  },
  methods: {
    disppear(data) {
      this.$set(this.$data, "isShow", data.isShow);
    },
  },
  mounted() {
    setTimeout(() => {
      this.$data.isShow = false;
    }, 1000);
  },
};
</script>

<style>
div.disppear {
  display: none;
}
</style>