<template>
  <!-- 中 - 左banner -->
  <div id="carousel_big">
    <span class="banner_arrow banner_arrow_left" id="btn-left"></span>
    <span class="banner_arrow banner_arrow_right" id="btn-right"></span>
    <ul class="transition" id="ul-imgs">
      <li v-for="(item,index) of items" v-bind:key="index">
        <a href="javascript:;"></a>
        <img :src="item" alt />
      </li>
    </ul>
    <ul id="ul-idxs">
      <li class="banner_actived"></li>
      <li></li>
      <li></li>
      <li></li>
      <li></li>
      <li></li>
      <li></li>
      <li></li>
    </ul>
  </div>
</template>

<script>
export default {
  name: "GouWuDaoHangCarouselBig",
  data() {
    return {
      items: [
        require("../../../../assets/images/index/banner_pic1.jpg"),
        require("../../../../assets/images/index/banner_pic2.jpg"),
        require("../../../../assets/images/index/banner_pic3.jpg"),
        require("../../../../assets/images/index/banner_pic4.jpg"),
        require("../../../../assets/images/index/banner_pic5.jpg"),
        require("../../../../assets/images/index/banner_pic6.jpg"),
        require("../../../../assets/images/index/banner_pic7.jpg"),
        require("../../../../assets/images/index/banner_pic8.jpg"),
        require("../../../../assets/images/index/banner_pic1.jpg"),
      ],
    };
  },
  methods: {},
  mounted() {
    var i = 0,
      LiWidth = 590,
      LiCount = 8,
      Duration = 500,
      ulImgs = document.getElementById("ul-imgs"),
      ulIdxs = document.getElementById("ul-idxs"),
      lis = ulIdxs.children;
    function moveTo(to) {
      if (to == undefined) {
        to = i + 1;
      }
      if (i == 0) {
        if (to > i) {
          ulImgs.className = "transition";
        } else {
          ulImgs.className = "";
          ulImgs.style.marginLeft = -LiWidth * LiCount + "px";
          setTimeout(() => {
            moveTo(LiCount - 1);
          }, 100);
          return;
        }
      }
      i = to;
      ulImgs.style.marginLeft = -i * LiWidth + "px";
      for (var li of lis) {
        li.className = "";
      }
      if (i == LiCount) {
        i = 0;
        setTimeout(() => {
          ulImgs.className = "";
          ulImgs.style.marginLeft = 0;
        }, Duration);
      }
      lis[i].className = "banner_actived";
    }
    var btnLeft = document.getElementById("btn-left"),
      btnRight = document.getElementById("btn-right"),
      canClick = true;
    btnRight.onclick = function () {
      move(1);
    };
    btnLeft.onclick = function () {
      move(-1);
    };
    function move(n) {
      if (canClick) {
        moveTo(i + n);
        canClick = false;
        setTimeout(() => {
          canClick = true;
        }, Duration);
      }
    }
    var banner = document.getElementById("carousel_big"),
      interval = 3000,
      timer = setInterval(() => {
        moveTo();
      }, interval);
    banner.onmouseover = function () {
      clearInterval(timer);
    };
    banner.onmouseout = function () {
      timer = setInterval(() => {
        moveTo();
      }, interval);
    };
    ulIdxs.onclick = function (e) {
      if (canClick) {
        var li = e.target;
        if (li.nodeName == "LI") {
          if (li.className !== "banner_actived") {
            for (var i = 0; i < lis.length; i++) {
              if (lis[i] == li) {
                break;
              }
            }
            moveTo(i);
            canClick = false;
            setTimeout(() => {
              canClick = true;
            }, Duration);
          }
        }
      }
    };
  },
};
</script>

<style scoped>
/* 中 - 左banner */

#carousel_big {
  width: 590px;
  height: 470px;
  position: relative;
  overflow: hidden;
}
#carousel_big > ul#ul-imgs {
  list-style: none;
  display: flex;
  width: 5310px;
}
#carousel_big > ul#ul-imgs.transition {
  transition: all 0.5s linear;
}
/* 图片 */

#carousel_big > ul#ul-imgs > li > img {
  width: 590px;
}

/* banner圆点 */

#ul-idxs {
  position: absolute;
  bottom: 20px;
  left: 25px;
  list-style: none;
  display: flex;
  flex-direction: row;
  align-items: center;
}

/* 圆点形态 */

#ul-idxs > li {
  width: 8px;
  height: 8px;
  background: rgba(0, 0, 0, 0.1);
  border-radius: 50px;
  margin: 0 2px;
  cursor: pointer;
}

/* banner圆点激活状态 */

.banner_actived {
  background: white !important;
  border: 3px solid rgba(0, 0, 0, 0.1);
}
</style>