<script>
export default {
  name: "GouWuDaoHangKuaiJieIconsJiPiao",
  data() {
    return {
      guoNeiRadio: "guoNeiDanCheng",
      guoJiRadio: "guoJiDanCheng",
      isGuoNeiDanCheng: true,
      isGuoJiDanCheng: true,
      items: [
        { title: "国内机票", class: true },
        { title: "国际/港澳台", class: false },
        { title: "特惠", class: false },
      ],
    };
  },
  methods: {
    change(i, e) {
      for (var item of this.items) {
        item.class = false;
      }
      this.items[i].class = true;
      var content = document.querySelector("div#grid_tab_plane");
      setTimeout(() => {
        content.style.marginLeft = -i * 160 + "px";
      }, 300);
    },
    danOrFanCheng(e) {
      switch (e.target.value) {
        case "guoNeiDanCheng":
          this.isGuoNeiDanCheng = true;
          break;
        case "guoNeiFanCheng":
          this.isGuoNeiDanCheng = false;
          break;
        case "guoJiDanCheng":
          this.isGuoJiDanCheng = true;
          break;
        case "guoJiFanCheng":
          this.isGuoJiDanCheng = false;
          break;
      }
    },
  },
};
</script>

