<template>
  <!-- 自动轮播广告 -->
  <div id="carousel-mini">
    <div class="content transition">
      <div class="block" v-for="(item, index) in items" :key="index">
        <img class="imgSrc" :src="item.imgSrc" alt />
        <div class="info">
          <p class="title">{{item.title}}</p>
          <p class="promote">{{item.promote}}</p>
          <p class="btn">{{item.button}}</p>
        </div>
      </div>
      <ul class="pointer">
        <li
          v-for="(i, idx) in items"
          :key="idx"
          :class="{active:i.active }"
          @mouseenter="change(idx)"
        ></li>
      </ul>
    </div>
  </div>
</template>

<script>
export default {
  name: "SecKillCarouselMini",
  data() {
    return {
      currentIndex: 0,
      timer: null,
      items: [
        {
          imgSrc: require("../../../assets/images/index/autoCountProduct1.webp"),
          title: "整机智能品类专场",
          promote: "笔记本低至3869元",
          button: "品类秒杀>",
          active: true,
        },
        {
          imgSrc: require("../../../assets/images/index/autoCountProduct2.webp"),
          title: "DIY游戏月末冲刺",
          promote: "爆款主机送显示器",
          button: "品类秒杀>",
          active: false,
        },
      ],
    };
  },
  methods: {
    change(i) {
      for (let i = 0; i < this.$data.items.length; i++) {
        this.$set(this.$data.items[i], "active", false);
      }
      this.$set(this.$data.items[i], "active", true);
      this.go(i);
    },
    go(i) {
      var content = document.querySelector("div#carousel-mini>div.content");
      this.currentIndex = i;
      if (i == undefined) {
        this.currentIndex++;
      }
      content.style.marginLeft = -this.currentIndex * 180 + "px";
    },
  },
};
</script>

