<template>
  <!-- 登录 - 输入框 -->
  <div id="loginbox-input-account">
    <div class="login_input user">
      <span class="icon user"></span>
      <input class="input" type="text" placeholder="邮箱/用户名/登录手机" v-model="value.userName" />
      <span class="close" :class="{disppear:isUserShow}" @click="clearInput('userName')"></span>
    </div>
    <div class="login_input password">
      <span class="icon password"></span>
      <input class="input" type="password" placeholder="密码" v-model="value.password" />
      <span class="close" :class="{disppear:isPswShow}" @click="clearInput('password')"></span>
    </div>
    <div class="forget">
      <a class="normalFont underline" href="javascript:;">忘记密码</a>
    </div>
    <div class="btn">登录</div>
  </div>
</template>

<script>
export default {
  name: "LoginInputAccount",
  data() {
    return {
      value: {
        userName: "",
        password: "",
      },
    };
  },
  computed: {
    isUserShow(e) {
      if (!this.$data.value.userName) {
        return true;
      } else {
        return false;
      }
    },
    isPswShow(e) {
      if (!this.$data.value.password) {
        return true;
      } else {
        return false;
      }
    },
  },
  methods: {
    clearInput(option) {
      this.$set(this.$data.value, option, "");
    },
  },
};
</script>

