<template>
  <div id="loginbox-input-QR">
    <!-- QR部分 -->
    <div class="QR">
      <div class="scan-this" :class="{to_center:transition}" @mouseenter="enter()">
        <img :src="qrSrc" alt />
      </div>
      <div class="tip" :class="{to_right:transition}">
        <img :src="tipSrc" alt />
      </div>
    </div>
    <!-- 文字提示 -->
    <div class="info-tip">
      <span>
        打开
        <a href="javascript:;">手机京东</a>
      </span>
      <span>扫描二维码</span>
    </div>
    <!-- 优势提醒 -->
    <div class="honor">
      <div>
        <div class="icon icon1"></div>
        <span>免输入</span>
      </div>
      <div>
        <div class="icon icon2"></div>
        <span>更快</span>
      </div>
      <div>
        <div class="icon icon3"></div>
        <span>更安全</span>
      </div>
    </div>
  </div>
</template>

<script>
export default {
  name: "LoginInputQR",
  data() {
    return {
      transition: true,
      qrSrc: require("../../../../../../assets/images/login/qr.png"),
      tipSrc: require("../../../../../../assets/images/login/phone-orange.png"),
    };
  },
  methods: {
    enter() {
      this.$data.transition = !this.$data.transition;
      if (!this.$data.transition) {
        setTimeout(() => {
          this.$data.transition = true;
        }, 2000);
      }
    },
  },
  mounted() {
    setTimeout(() => {
      this.$data.transition = false;
    }, 200);
    setTimeout(() => {
      this.$data.transition = true;
    }, 4000);
  },
};
</script>

