<template>
  <!-- 中 - 右banner -->
  <div id="carousel_small">
    <span class="banner_arrow banner_arrow_left" v-on:click="change(-1)"></span>
    <span class="banner_arrow banner_arrow_right" v-on:click="change(1)"></span>
    <ul>
      <li id="li-imgs" v-bind:class="{active:isActived}">
        <div v-for="(item,index) of items[num]" v-bind:key="index">
          <img :src="item" alt />
        </div>
      </li>
    </ul>
  </div>
</template>

<script>
export default {
  name: "GouWuDaoHangCarouselSmall",
  data() {
    return {
      isActived: true,
      Duration: 400,
      CanClick: true,
      num: 0,
      items: [
        [
          require("../../../../assets/images/index/banner_sm_pic1.webp"),
          require("../../../../assets/images/index/banner_sm_pic2.webp"),
          require("../../../../assets/images/index/banner_sm_pic3.webp"),
        ],
        [
          require("../../../../assets/images/index/banner_sm_pic4.webp"),
          require("../../../../assets/images/index/banner_sm_pic5.webp"),
          require("../../../../assets/images/index/banner_sm_pic6.webp"),
        ],
        [
          require("../../../../assets/images/index/banner_sm_pic7.webp"),
          require("../../../../assets/images/index/banner_sm_pic8.webp"),
          require("../../../../assets/images/index/banner_sm_pic9.webp"),
        ],
      ],
    };
  },
  methods: {
    change(i) {
      if (this.CanClick) {
        this.isActived = false;
        setTimeout(() => {
          if (this.num + i == -1) {
            this.num = 2;
          } else if (this.num + i == 3) {
            this.num = 0;
          } else {
            this.num += i;
          }
          this.isActived = true;
          this.CanClick = false;
          setTimeout(() => {
            this.CanClick = true;
          }, this.Duration);
        }, 100);
      }
    },
  },
};
</script>

