  methods: {
    gotoPage(i) {
      if (this.canClick) {
        this.currentIndex = i.num;
        this.isActive = i.check;
        var content = document.querySelector("div#carousel>div.content");
        if (!this.isActive) {
          if (i.direction == "left") {
            content.style.marginLeft = -this.currentIndex * 800 + "px";
            setTimeout(() => {
              this.gotoPage({ num: this.items.length - 2, check: true });
            }, 150);
          } else if (i.direction == "right") {
            content.style.marginLeft = -this.currentIndex * 800 + "px";
            setTimeout(() => {
              this.gotoPage({ num: 1, check: true });
            }, 150);
          }
          return;
        }
        content.style.marginLeft = -this.currentIndex * 800 + "px";
        this.canClick = false;
        setTimeout(() => {
          this.canClick = true;
        }, 500);
      }
    },
  },
  computed: {
    prevIndex() {
      if (this.currentIndex == 0) {
        var num = this.items.length - 1;
        return { num, check: false, direction: "left" };
      } else {
        var num = this.currentIndex - 1;
        return { num, check: true };
      }
    },
    nextIndex() {
      if (this.currentIndex == this.items.length - 1) {
        var num = 0;
        return { num, check: false, direction: "right" };
      } else {
        var num = this.currentIndex + 1;
        return { num, check: true };
      }
    },
  },
};
